import React, { useMemo } from 'react'
import { graphql } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'
import Banner from '../../containers/HomePage/Banner'

const Mobile = ({ data: { allMobileJson: { nodes } } }) => {
  const images = useMemo(() => nodes.map(layer => ({
    ...layer,
    image: getImage(layer.image),
  })).reverse(), [nodes])

  return (
    <Banner
      width={834}
      height={717}
      images={images}
    />
  )
}

export default Mobile

export const query = graphql`
query MobileBannerQuery {
  allMobileJson {
    nodes {
      name
      width
      height
      x
      y
      depth
      image {
        childImageSharp {
          gatsbyImageData(
            layout: FULL_WIDTH
          )
        }
      }
    }
  }
}
`
