import { AspectRatio, Box } from '@chakra-ui/react'
import React, { useRef } from 'react'
import { GatsbyImage, StaticImage } from "gatsby-plugin-image"
// import Parallax from 'parallax-js'


const Banner = ({ images, width, height }) => {
  const ref = useRef()
  // useEffect(() => {
  //   new Parallax(ref.current, {
  //     relativeInput: true
  //   });
  // }, [])
  return (
    <Box position="relative">
      <StaticImage src="../../image/mobile-bg.jpg" alt="背景" />
      <AspectRatio position="absolute" left="0" right="0" top="0" ratio={width / height} >
        <Box height="100%" ref={ref}>
          {images.map((layer, i) => (
            // <Box key={i} data-depth={layer.depth} height="100%" width="100%" position="absolute">
              <GatsbyImage
                key={i}
                image={layer.image}
                alt={layer.name}
                style={{
                  position: 'absolute',
                  width: `${layer.width / width * 100}%`,
                  height: `${layer.height / height * 100}%`,
                  left: `${layer.x / width * 100}%`,
                  top: `${layer.y / height * 100}%`,
                }}
              />
            // </Box>
          ))}
        </Box>
      </AspectRatio>
    </Box>
  )
}

export default Banner
